
import Cookies from 'js-cookie'

export function asideReduce(state = 'hide', action) { //aside

    if (action.type === 'ASIDE') {
        return action.payload
    }
    return state
}

export function loginUIREduce(state = { type: 'login', status: 'hide' }, action) { //UI
    if (action.type === 'CHANGE') {
        return {
            ...action.payload
        }
    }
    return state
}

export function imgReduce(state = { visible: false, src: '/resouce/images/fly.jpg' } , action){
    if (action.type === 'VIEW'){
        console.log(action.payload)
        return {
            ...action.paylaod
        }
    }
    return state
}

export const fullScreenReduce = (state = false, action) => {
    if (action.type === 'TOGGLE_SCREEN') {
        return !state
    }
    return state
}

export function toTopReducer(state = { visible: false, cb: () => { } }, action) {
    if (action.type === 'HIDE') {
        // window.debug() && console.log(action)
        state.visible = false
        return state
    }
    if (action.type === 'SHOW') {
        action.payload && action.payload.cb && action.payload.cb()
        return {
            visible: true
        }
    }
    return state
}

