const datas = {  //
    dynamics: [],
    articles: [],
    messages: [],
    avatars: []
}

export function allData(state = {}, action) {  //
    if (action.type === 'SET_DATA') {
        window.debug() && console.log(action.payload.data)
        datas[action.payload.type] = action.payload.data
        return datas
    }
    if (action.type === 'UPDATE_DATA') {
        window.debug() && console.log(action.payload.data)
        for (let item of datas[action.payload.type]) {
            if (item._id === action.payload.data._id) {
                item.title = action.payload.data.title
                item.content = action.payload.data.content
                break
            }
        }
        return datas
    }
    return datas
}

export function dialogReducer(state = {}, action) {  //dialog
    if (action.type === 'QUERY_DYNAMIC') { //dynamic
        // console.log(action.payload)
        if (action.payload.dynamic && action.payload.dynamic._id) {
            return {
                dynamic: action.payload.dynamic,
                visible: true,
                cb: action.payload.cb,
                dialogType: 'editDynamic'
            }
        }
        if (!action.payload.visible)
            return { visible: false }
    }
    if (action.type === 'NEW_DYNAMIC') { //dynamic
        return {
            dynamic: {},
            cb: action.payload.cb,
            placeholder: action.payload.placeholder,
            dialogType: 'newDynamic',
            visible: true
        }
    }
    if (action.type === 'LEAVE_MESSAGE') { //
        return {
            dialogType: 'leaveMessage',
            visible: true,
            count: action.payload.count || 100,
            placeholder: action.payload.placeholder,
            cb: action.payload.cb,
            cancelCallback: action.payload.cancelCallback || undefined
        }
    }
    return { visible: false }
}

export function logoReducer(state = { src: 'home', item: '' }, action) {  //logo
    if (action.type === 'LOGO')
        return action.payload
    return state
}

export function isLoginReduce(state = { status: false, text: '' }, action) {  //
    if (action.type === 'SET_USER_VO') {
        return {
            ...action.payload,
            status: true
        }
    }
    if (action.type === 'CLEAR_USER') {
        Cookies.remove('user')
        Cookies.remove('token')
        // if (!!sessionStorage.getItem('user')) {
        // const d = new Date(-1)
        // document.cookie = 'user=' + sessionStorage.getItem('user') + ';path=/;expires=' + d
        sessionStorage.removeItem('user')
        return {
            status: false,
            text: ''
        }
        // }
    }
    return state
}