import React from 'react';
import './login.css'
import { connect } from 'react-redux';
import { Icon, Row, Col, Button, Form, Checkbox, Input } from 'antd'
import { Toast } from 'antd-mobile'
import { Base64 } from 'js-base64'
import { API } from '../../request/request'
import Cookies from 'js-cookie'

const FormItem = Form.Item
const UI = props => {
    const { getFieldDecorator } = props.form;
    const handleSubmit = e => {
        e.preventDefault();
        props.form.validateFields((err, values) => {
            if (!err) {
                if (/ /.test(values.name)) {
                    Toast.fail('', 1)
                    return
                }
                if (values.name.length > 20) {
                    Toast.fail('20', 1)
                    return
                }
                if (/ /.test(values.pwd)) {
                    Toast.fail('', 1)
                    return
                }
                if (values.pwd.length > 20) {
                    Toast.fail('16', 1)
                    return
                }
                if (props.type === 'register' && values.pwd !== values.pwd2) {
                    Toast.fail('', 1)
                    return
                }
                values.pwd = Base64.encode(values.pwd)
                delete values.pwd2
                Toast.loading(props.type + '...', 3)
                API(props.type === 'login' ? '/login' : '/register', 'POST', values).then(result => {
                    if (result.success) {
                        Toast.success(props.type === 'login' ? '' : '', 1, props.type === 'login' ? (() => {
                            props.userInfo({ ...result, text: '' })
                            sessionStorage && sessionStorage.setItem('user', Base64.encode(values.name))
                            Cookies.set('token', result.token)
                            Cookies.set('user', Base64.encode(values.name))
                            props.change('login', 'hide')
                        }) : (() => {
                            props.change('login', 'open')
                        }))
                    } else
                        Toast.fail(result, 1)
                })
            }
        });
    }
    function resetView() {
        document.getElementsByClassName('container')[0].scrollIntoView(false)
    }
    return (
        <div className={props.status === 'hide' ? 'loginRegContainer' : 'loginRegContainer showContainer'}>
            <div className="backIcon" onClick={() => props.change(props.type, 'hide')}>
                <Icon type="left" />

            </div>
            <Row type='flex' justify='center'>
                <Col span={10} className='loginLogo'>
                    <div className='loginLogoDiv'>
                        <Icon type="pie-chart" />
                    </div>
                    <div className='loginLogoDiv'>
                        Welcome
                    </div>
                </Col>
            </Row>
            <Row type='flex' justify='center' style={{ marginTop: '30px' }}>
                <Col span={23}>
                    <Form onSubmit={(e) => handleSubmit(e)} className="login-form">
                        <FormItem>
                            {getFieldDecorator('name', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input onBlur={() => resetView()} autoComplete={props.type === 'register' ? 'true' : 'false'} className='_input' prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                            )}
                        </FormItem>
                        <FormItem>
                            {getFieldDecorator('pwd', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input onBlur={() => resetView()} autoComplete={props.type === 'register' ? 'true' : 'false'} className='_input' prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                            )}
                        </FormItem>
                        {
                            props.type === 'register' &&
                            <FormItem>
                                {getFieldDecorator('pwd2', {
                                    rules: [{ required: true, message: '' }],
                                })(
                                    <Input onBlur={() => resetView()} autoComplete={props.type === 'register' ? 'true' : 'false'} className='_input' prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                                )}
                            </FormItem>
                        }
                        {props.type === 'login' &&
                            <FormItem>
                                {getFieldDecorator('state', {
                                    valuePropName: 'checked',
                                    initialValue: true,
                                })(
                                    <Checkbox></Checkbox>
                                )}
                            </FormItem>
                        }
                        <FormItem>
                            <Button type="primary" htmlType="submit" className="login-form-button">
                                {props.type === 'login' ? '' : ''}
                            </Button>
                        </FormItem>
                    </Form>
                </Col>
            </Row>
        </div>
    )
}
const WrappedNormalLoginForm = Form.create()(UI);

export const LoginReg = connect(state => {
    return {
        ...state.loginUI
    }
}, dispatch => {
    return {
        change(type, status) {
            dispatch({ type: 'CHANGE', payload: { type, status } })
        },
        userInfo(user) {
            dispatch({ type: 'SET_USER_VO', payload: { ...user } })
        },
    }
})(WrappedNormalLoginForm)