import React from 'react'
import './message.css'
import { Card, Toast, Modal } from 'antd-mobile'
import { connect } from 'react-redux'
import { Icon } from 'antd'
import { API } from '../../request/request'
import { Scroll } from '../../handleOnScroll/scroll';

const alert = Modal.alert;
export class _UI extends React.Component {
    constructor() {
        super()
        this.state = {
            data: [],
            avatar: [],
            names: [],
            height: document.documentElement.clientHeight,
            refreshing: false,
            scrollHeight: 0,
            index: 0,//sliceindex, pagesize
            pageSize: 6,//
            isOver: false,//         
        }
    }
    shouldComponentUpdate(arg1, arg2) {
        if (arg2.data.length === 0) return false
        return true
    }
    componentWillReceiveProps(props) {
        const isFullScreen = props.screen
        this.setState({
            scrollHeight: isFullScreen ? window.innerHeight : window.innerHeight - 110
        })
    }
    componentDidMount() {
        this.props.changeTop('HIDE')                
        this.setState({
            scrollHeight: window.innerHeight - 110
        })
        const { index, pageSize } = this.state
        if (window.sessionStorage && window.sessionStorage.getItem('messages')) {
            const data = JSON.parse(window.sessionStorage.getItem('messages'))
            const names = [...new Set(data.map(item => item.name))]
            this.getAvatars(names)
            this.setState({
                data,
                index: 6,
                pageSize: 12
            })
        } else {
            Toast.loading('...', 30)
            this.sendFetch(index, pageSize, () => {
                Toast.hide()
                this.getAvatars.bind(this)
            })
        }
        const scroll = new Scroll(this.body)
        scroll.onScrollEnd(this.handleScroll.bind(this))
        scroll.onScroll(() => {
            const { toTopVisible, changeTop } = this.props
            const { body } = this
            if (body.scrollTop <= 0) {
                toTopVisible && changeTop('HIDE')
            }
            else {
                changeTop('SHOW')
            }
        })
    }
    concatData(data, temp) {
        const copyData = JSON.parse(JSON.stringify(data))
        const allId = copyData.map(item => item._id)
        for (let item of temp) {
            !(allId.includes(item._id)) && copyData.push(item)
        }
        return [...copyData]
    }
    sendFetch(index, pageSize, cb) {
        if (this.state.isOver) return
        API('/getMessageByPageSize', 'POST', { index, pageSize }).then(result => {
            if (result.success) {
                index === 0 && window.sessionStorage && window.sessionStorage.setItem('messages', JSON.stringify(result.data))                                
                const data = this.concatData(this.state.data, result.data)
                const names = [...new Set(data.map(item => item.name))]
                this.getAvatars(names)
                const _index = this.state.pageSize
                const pageSize = _index + 6
                const isOver = result.isOver
                this.setState(state => {
                    return {
                        data,
                        index: _index,
                        pageSize,
                        isOver,
                        names
                    }
                }, cb && cb())
            }
        })
    }
    handleScroll() {
        if (this.state.isOver) return
        const { body } = this
        if (body.scrollTop + body.clientHeight >= body.scrollHeight) {
            this.sendFetch(this.state.index, this.state.pageSize)
        }
    }
    refresh() {
        this.setState({ refreshing: true })
        API('/getAllMessages').then(result => {
            if (result.success) {
                this.setState({ data: result.data })
                this.props.setData({ payload: { type: 'messages', data: result.data } })
                const names = []
                for (let item of result.data)
                    names.push(item.name)
                this.setState({
                    names: [...new Set(names)],
                }, () => {
                    this.getAvatars(() => this.setState({ refreshing: false }))
                })
            }
            else Toast.offline('', 1)
        })
    }
    getAvatars(names) {
        API('/all_user_avatar', 'POST', { name: names }).then(result => {
            if (result.success) {
                this.setState({ avatar: result.data })
            }
        })
    }
    setAvatar(name) {
        for (let item of this.state.avatar)
            if (name === item.name)
                return item.avatar
        return ''
    }
    toggleList(_id) {
        const { data } = this.state
        for (let item of data)
            if (item._id === _id)
                item.open = !item.open
        this.setState({
            data: [...data]
        })
    }
    handleRepeat(item, _id, placeholder) {
        if (this.props.user.name === item.name) {
            Toast.offline('', 2)
            return
        }
        this.openDialog(_id, item.name, placeholder)
    }
    deleteMsg(_id, type, _parent_id) {
        console.log(_id)
        Toast.loading('', 10)
        if (type === 'outer') {
            API('/deleteMsgById', 'POST', { _id, operator: this.props.user.name }).then(result => {
                Toast.hide()
                if (result.success) {
                    const data = this.state.data.filter(item => item._id !== _id)                   
                    this.setState({ data }, () => window.sessionStorage.setItem('messages', JSON.stringify(this.state.data.slice(0, 6))))
                    Toast.success('', 2)
                } else Toast(result)
            })
        }
        else if (type === 'inner') {
            API('/deleteInnerRepeat', 'POST', { _id, _parent_id, name: this.props.user.name }).then(result => {
                Toast.hide()
                if (result.success) {
                    const { data } = this.state
                    for (let item of data) {
                        if (item._id === _parent_id) {
                            const repeat = item.repeat.filter(msg => msg._id !== _id)
                            item.repeat = repeat
                            break
                        }
                    }
                    this.setState({ data })
                    this.props.setData({ payload: { type: 'messages', data } })
                    Toast.success('', 2)
                } else Toast(result)
            })
        }
    }
    renderMsg(data, type, _parent_id) {
        if (type === 'repeat') {
            if (data.length === 0)
                return (
                    <div style={{ lineHeight: '20px' }} className="repeatItemContainer">
                        
                    </div>
                )
            return <div className="repeatItemContainer"  >
                <div className="dot"></div>
                {
                    data && data.map(item => {
                        return (
                            <div className="repeatItem" key={item._id}>
                                <div className="repeatStatus">
                                    <span>{item.name} {item.date} {item.toRepeat ? ' @ ' + item.toRepeat + ' ' : '  '}</span>
                                    <Icon className='repeatInner' onClick={() => this.props.user.name ? this.handleRepeat(item, _parent_id, item.name) : Toast.fail('', 1)} type='plus'></Icon>
                                    {
                                        (this.props.user.admin || this.props.user.name === item.name) &&
                                        <Icon style={{ marginRight: '46px' }} className='repeatInner deleteI' onClick={() => alert('', '', [
                                            { text: '', onPress: () => console.log('cancel') },
                                            { text: '', onPress: () => this.deleteMsg(item._id, 'inner', _parent_id) }
                                        ])} type='delete' />}
                                </div>
                                <div className="repeatText">{item.info}</div>
                            </div>

                        )
                    })
                }
            </div>
        }
        return data && data.map(item => {
            return (
                <div className="messageItem " key={item._id}>
                    <Card >
                        <Card.Header
                            className='itemFromLeft'
                            title={<span><div style={{ paddingLeft: '7px', color: '#0056a0' }}>{item.name}</div><div className='messageDate'>{item.date.replace(/-----/, ' ')}</div></span>}
                            thumb={this.setAvatar(item.name)}
                        />
                        <Card.Body className=''>
                            <div style={{ color: '#4089bc', marginLeft: '53px', fontSize: '14px', lineHeight: '30px', whiteSpace: 'pre-wrap', textAlign: 'left' }}>{item.content}</div>
                        </Card.Body>
                        <Card.Footer
                            className=''
                            extra={
                                <div className="messageOperationContainer" style={{ textAlign: 'center' }}>
                                    {(this.props.user.admin || this.props.user.name === item.name) &&
                                        <div onClick={() => alert('', '', [
                                            { text: '', onPress: () => console.log('cancel') },
                                            { text: '', onPress: () => this.deleteMsg(item._id, 'outer') }
                                        ])}>
                                            <Icon className='deleteI' style={{ position: 'relative', top: '-2px' }} type='delete' />
                                        </div>
                                    }
                                    <div style={{ fontSize: '12px', color: '#4089bc' }} onClick={() => this.toggleList(item._id)}>
                                        <span style={{ paddingRight: '5px' }}>{item.repeat.length || 0}</span>
                                        <Icon type='message' />
                                    </div>
                                    {<div style={{ fontSize: '12px', color: '#4089bc' }} onClick={() => this.props.user.name ? this.openDialog(item._id, this.props.user.name === item.name ? null : item.name, this.props.user.name === item.name ? '' : item.name) : Toast.fail('', 1)} >
                                        <Icon type='plus' />
                                    </div>
                                    }
                                </div>} />
                    </Card>
                    {
                        item.open && item.repeat && this.renderMsg(item.repeat.reverse(), 'repeat', item._id)
                    }
                </div>
            )
        })
    }
    openDialog(_id, toRepeat, placeholder) {
        if (!!_id) {  //
            const cb = data => {
                window.debug() && console.log(data.content)
                if (!data.content || data.content.trim() === '') {
                    Toast.fail('', 1)
                    return
                }
                let date = new Date()
                let minute = date.getMinutes()
                const m = minute < 10 ? minute + 1 : '0' + minute
                let seconds = date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds()
                let hour = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours()
                let year = date.getFullYear()
                let month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1
                let day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
                let d = year + '/' + month + '/' + day + ' ' + hour + ':' + m + ':' + seconds
                Toast.loading('', 1)
                API('/repeatMsg', 'POST', {
                    _id,
                    msg: {
                        info: escapeData(data.content),
                        name: this.props.user.name,
                        date: d,
                        toRepeat
                    }
                }).then(result => {
                    if (result.success) {
                        data.hide({ visible: false })
                        this.setState({ data: result.data.reverse() }, () => {
                            Toast.loading('', 1)
                            this.getAvatars()
                            window.sessionStorage.setItem('messages', JSON.stringify(this.state.data.slice(0, 6)))    
                        })
                    }
                    else Toast.offline(result)
                })
                return
            }
            this.props.openDialog({ count: 180, cb, placeholder: toRepeat === null ? '' : ' ' + placeholder + ' ' })
            return
        }
        //
        this.refs._sendMsg.children[0].style.transform = 'rotate(45deg)'
        const cb = data => {
            window.debug() && console.log(data)
            if (!data.content || data.content.trim() === '') {
                Toast.offline('', 1)
                return
            }
            Toast.loading('...', 3)
            const msg = escapeData(data.content)
            const name = this.props.user.name
            let date = new Date()
            let minute = date.getMinutes()
            const m = minute < 10 ? '0' + minute : minute
            let hour = date.getHours()
            const h = hour < 10 ? '0' + hour : hour
            let year = date.getFullYear()
            let month = date.getMonth() + 1
            const ms = month < 10 ? '0' + month : month
            let day = date.getDate()
            const _day = day < 10 ? '0' + day : day
            let d = year + '-' + ms + '-' + _day + '-----' + h + ' : ' + m
            API('/leaveMessage', 'POST', { date: d, content: msg, name }).then(result => {
                if (result.success) {
                    this.refs._sendMsg.children[0].style.transform = 'rotate(0deg)'
                    this.setState({ data: [...result.data.reverse()] }, () => {
                        Toast.loading('', 1)
                        window.sessionStorage.setItem('messages', JSON.stringify(this.state.data.slice(0, 6)))                     
                    })
                    data.hide({ visible: false })
                } else Toast.offline(result)
            })
        }
        this.props.openDialog({ count: 180, cb, placeholder: '...', cancelCallback: () => this.refs._sendMsg.children[0].style.transform = 'rotate(0deg)' })
    }
    render() {
        const { scrollHeight } = this.state
        // setTimeout(() => {
        //     const els = document.getElementsByClassName('messageItem')
        //     let k = 0
        //     for (let i = 0; i < els.length; i++) {
        //         console.log(i + 1)
        //         els[i].style['animation-delay'] = `${(i + 1 % 6 === 0 ? 0 : i / 8) / 4}s`
        //     }
        //     k = 0
        // }, 0);
        return (
            <div className="messageContainer contentSlideFromLeft" style={{ height: scrollHeight + 'px' }} ref={body => this.body = body}>
                <div ref='_sendMsg' className="sendMsg" onClick={() =>
                    this.props.user.name ?
                        (this.openDialog(),
                            toggleRotate(this.refs._sendMsg.children[0])
                        ) : Toast.fail('', 1)} >
                    <Icon className='sendMsgI' type='plus' />
                </div>
                {
                    this.renderMsg(this.state.data)
                }
                {
                    !this.state.isOver &&
                    <div className='loadingCon'>
                        <Icon type='loading' />
                    </div>
                }
            </div>
        )
    }
}

function escapeData(data) {
    return data.replace(/<input\stype="text"\sdata-formula="e=mc\^2"\sdata-link="quilljs\.com"\sdata-video="Embed\sURL"\splaceholder="Embed\sURL">/g, '').replace(/<\/?script>+|+|+|+|SB+|sB+|sb+|+|/g, '**');
}
function toggleRotate(el) {
    el.style.transform = 'rotate(45deg)'
    const els = document.querySelectorAll('.am-modal-button')
    for (let item of els)
        item.addEventListener('click', () => {
            el.style.transform = 'rotate(0deg)'
        })

}
export const Message = connect(state => {
    return {
        user: state.user,
        messages: state.datas.messages,
        avatars: state.datas.avatars,
        toTopVisible: state.toTop.visible,
        screen: state.screen        
    }
}, (dispatch) => {
    return {
        setData(params) {
            dispatch({ type: 'SET_DATA', ...params })
        },
        openDialog(payload) {
            dispatch({ type: 'LEAVE_MESSAGE', payload })
        },
        changeTop(type) {
            dispatch({ type })
        },
    }
})(_UI)